.app_footer {
    width: 100%;
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #a5986d;
    padding-top: 0;
}

.app_footer-links {
    width: 100%;
    max-width: 1200px;
    display: flex;
    justify-content: center;
    align-items: space-evenly;
    margin-top: 5rem;
    padding: 0 2rem;
    gap: 2rem;
}

.app_footer-links_contact,
.app_footer-links_logo,
.app_footer-links_work {
    flex: 1;
    margin: 0 1rem; 
    text-align: center;
}

.app_footer-links_logo img:first-child {
    width: 290px;
    margin-bottom: 0.75rem;
}

.app_footer-links_work p:nth-child(2n + 1){
    margin-bottom: 1rem;
}

.app_footer-links_icons {
    margin-top: 0.5rem;
}

.app_footer-links_icons svg {
    margin: 0.5rem;
    font-size: 24px;
    cursor: pointer;
}

.app_footer-links_icons svg:hover {
    color: rgb(216, 216, 216);
}

.app_footer-headtext {
    color: #027c4d;
    font-weight: 300;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    font-size: 32px;
    line-height: 41.6px;
    margin-bottom: 1rem;
}

.footer_copyright {
    margin-top: 3rem;
}

@media screen and (min-width: 2000px) {
    .app_footer-headtext {
        font-size: 51px;
        line-height: 61px;
    }
}

@media screen and (max-width: 1150px) {
    .app_footer-links {
        align-items: center;
        flex-direction: column;
        padding: 0;
    }

    .app_footer-links_contact,
    .app_footer-links_logo,
    .app_footer-links_work {
        margin: 2rem 0;
        width: 100%;
    }
}

@media screen and (max-width: 650px) {
    .app_footer{
        padding: 0 0 2rem 0;
    }
}

@media screen and (max-width: 350px) {
    .app_footer-links_logo img:first-child {
        width: 80%;
    }
}