.app_newsletter {
    padding: 2rem 4rem;
    border: 1px solid var(--color-black);
    background: rgb(211, 197, 140);
} 

.app_newsletter-heading {
    text-align: center;
}

.app_newsletter-input {
    flex-direction: row;
    margin-top: 3rem;
}

.app_newsletter-input input {
    width: 620px;
    border: 1px solid var(--color-black);
    border-radius: 5px;
    font-size: 1rem;
    font-family: var(--font-base);
    color: rgb(0, 0, 0);
    margin-right: 2rem;
    padding: 0.75rem 1rem;
}

.app_newsletter-input button {
    width: max-content;
}

@media screen and (min-width: 2000px) {
    .app_newsletter-input input {
        font-size: 2rem;
    }
}

@media screen and (max-width: 990px) {
    .app_newsletter-input {
        flex-direction: column;
        width: 100%;
    }

    .app_newsletter-input input {
        margin: 0 0 2rem 0;
        width: 100%;
    }
}

@media screen and (max-width: 650px) {
    .app_newsletter {
        padding: 2rem 0;
        border: none;
    }
}

@media screen and (max-width: 300px) {
    .app_newsletter-heading h1 {
        font-size: 32px;
        line-height: 50px;
    }
}