.app_specialMenu {
    flex-direction: column;
    background: rgb(211, 197, 140);
}

.app_specialMenu-title {
    margin-bottom: 2rem;
    text-align: center;
}

.app_specialMenu-menu {
    width: 100%;
    margin: 2rem 0;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: row;
}

.app_specialMenu_menu_heading {
    font-family: var(--font-base);
    font-weight: 600;
    font-size: 45px;
    line-height: 58.5px;
    letter-spacing: 0.04em;
}

.app_specialMenu-menu_wine,
.app_specialMenu-menu_cocktails {
    flex: 1;
    width: 100%;
    flex-direction: column;

}

.app_specialMenu-menu_img {
    width: 610px;
    margin: 0 2rem;
}

.app_specialMenu-menu_img img {
    width: 100%;
    height: auto;
}

.app_specialMenu_menu_items {
    display: flex;
    flex-direction: column;
    margin: 2rem 0;
    width: 100%;
}

@media screen and (max-width: 2000px) {
    .app_specialMenu-menu_img {
        width: 650px;
    }
}

@media screen and (max-width: 1150px) {
    .app_specialMenu-menu {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .app_specialMenu_menu_heading {
        font-size: 35px;
        line-height: 48px;
    }

    .app_specialMenu-menu_img {
        margin: 3rem 0;
    }
}

@media screen and (max-width: 650px) {
    .app_specialMenu-menu_img {
        width: 100%;
    }
}